.email,
.endereo-de-email {
  position: absolute;
  top: 0;
  left: 0;
}
.endereo-de-email {
  font-weight: 600;
}
.email {
  width: 400px;
  height: 79px;
}
.lock-icon {
  width: 24px;
  height: 24px;
}
.div,
.lock-icon {
  position: relative;
}
.passoword,
.sua-senha {
  position: absolute;
  left: 0;
}
.passoword {
  top: 31px;
  border-radius: var(--br-9xs);
  background-color: var(--gray-800);
  width: 400px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-base);
  box-sizing: border-box;
  gap: var(--gap-xs);
}
.sua-senha {
  top: 0;
  font-size: var(--font-size-base);
  font-weight: 600;
  color: var(--gray-100);
}
.passoword-parent {
  position: absolute;
  top: 95px;
  left: 0;
  width: 400px;
  height: 79px;
  font-size: var(--font-size-sm);
  color: var(--dray-400);
}
.entrar-na-plataforma {
  position: relative;
  font-weight: 600;
}
.entrar-na-plataforma-wrapper {
  position: absolute;
  top: 246px;
  left: 0;
  border-radius: var(--br-9xs);
  background-color: var(--cryan-500);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 400px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-base);
  box-sizing: border-box;
  color: var(--black);
}
.check-icon {
  position: relative;
  width: 20px;
  height: 20px;
}
.check-wrapper {
  border-radius: var(--br-9xs);
  background-color: var(--gray-800);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-11xs);
}
.react-2-1 {
  position: relative;
  width: 2194px;
  height: 2500px;
  overflow: hidden;
  flex-shrink: 0;
}
.frame-parent {
  position: absolute;
  top: 190px;
  left: 0;
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  font-size: var(--font-size-sm);
  color: var(--gray-200);
}
.form {
  position: absolute;
  top: 445px;
  left: 520px;
  width: 400px;
  height: 289px;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--gray-100);
  font-family: var(--font-inter);
}
