@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;

  /* Colors */
  --gray-800: #202020;
  --gray-900: #121214;
  --dray-400: #7c7c8a;
  --gray-100: #e1e1e6;
  --gray-200: #c4c4cc;
  --cryan-500: #81d8f7;
  --black: #000;
  --cyan-300: #aae9ff;

  /* Gaps */
  --gap-5xs: 8px;
  --gap-xs: 12px;

  /* Paddings */
  --padding-11xs: 2px;
  --padding-xs: 12px;
  --padding-base: 16px;

  /* Border radiuses */
  --br-9xs: 4px;
}
